// carousel

#pfcarousel {
    padding: 0;
    margin-top: -19px;

    .item-inner {
        height: 400px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;

        & > div {
            width: 100%;
            height: 100%;
            background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 50%, rgba(0,0,0,.7) 80%)
        }
    }

    h2, p {
        text-shadow: 1px 1px 1px #000;
        font-weight: 500;
        color: white;
        margin-bottom: 0
    }

    p {
        font-size: 18px;
        color: #fff;
        text-shadow: 0 0 5px black
    }
    
    .carousel-caption {
        position: initial;
        bottom: auto
    }
}

.carousel-control {
    height: 400px;
    padding-top: 200px
}

// index lists

.list-news li:before {
    font-family: 'FontAwesome';
    content: '\f1ea';
    margin: 0 5px 0 -15px
}

.list-spielehilfen li:before {
    font-family: 'FontAwesome';
    content: '\f0f6';
    margin: 0 5px 0 -15px
}

.list-spielehilfen li {
    list-style-type: none
}